<template>
   <div class="setpasswd">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>登录密码</span>
        </div>
        <div class='set_cen'>
            <van-cell-group>
                <van-field 
                label="手机号"
                disabled
                :placeholder="phone" />
            </van-cell-group>
            <van-cell-group>
                <van-field
                    center
                    clearable
                    v-model="code"
                    label="验证码"
                    placeholder="请输入验证码">
                    <van-button class='passwd_but' slot="button" size="small" @click='getCode()' type="primary">发送验证码</van-button>
                </van-field>
            </van-cell-group>
            <van-cell-group>
                <van-field 
                label="新密码"
                type="password"
                v-model="password"
                placeholder="请输入密码" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                label="确认密码"
                type="password"
                v-model="passwd"
                placeholder="请确认密码" />
            </van-cell-group>
        </div>
        <van-button size="large" class='set_but' @click='submit()'>确认修改</van-button>
   </div>
</template>

<script>


export default {
   name: 'setpasswd',
   data() {
       return {
           phone:localStorage.phone,
           code:'',
           password:'',
           passwd:'',
           sid:''
       }
   },

   methods:{
        retreat(){
            this.$router.go(-1);
        },
        getCode(){
            let url = '/userLogin/sendCode';
            this.$http.post(url,{
                cmd:'102',
                body:{
                    accessToken:localStorage.token,
                    did:'',
                    phone:this.phone
                }
            }).then( (res) => {
                // console.log(res)
                this.sid = res.sid;
            } )
        },
        submit(){
            if( this.code === '' ){
                this.$dialog.alert({
                    message: '请输入验证码'
                })
            }else if( this.password === '' ){
                this.$dialog.alert({
                    message: '请输入密码'
                })
            }else if( this.password !== this.passwd ){
                this.$dialog.alert({
                    message: '两次输入密码不一致'
                })
            }else{
                let url = '/userLogin/setPasswd';
                this.$http.post(url,{
                    cmd:'102',
                    body:{
                        accessToken:localStorage.token,
                        did:'',
                        phone:this.phone,
                        code:this.code,
                        sid:this.sid,
                        passwd:this.password,
                        passwd_again:this.passwd
                    }
                }).then( (res) => {
                    // console.log(res);
                    this.$router.go(-1);
                } )
            }
        }
   },
  components: {}
}
</script>

